.form-input {
    display: flex;
    flex-direction: column;
    gap: 40px;
}
.input-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 50px;
}

.my-fieldset {
  width: 32%;
  border-radius: 0px 9px;
}

legend {
  font-size: 0.65rem;
  padding: 3px 5px;
}

.input-container .my-fieldset input {
  background-color: transparent;
  border: none;
  width: 100%;
  padding: 7px;
  color: orange;
  outline: none;
  font-size: 1.1rem;
}

